<template>
	<div class="diy_list page_vaccine_appointment" id="vaccine_appointment_list">
		<div class="warp">
			<div class="container diy_list_container">
				<div class="diy_list_title">
					<div class="col">
						<span class="title">疫苗预约列表</span>
					</div>
				</div>
				<div class="row diy_list_search">
					<div class="col">
						<!-- 搜索栏 -->
						<div class="view">
							<span class="diy_list_search_title">关键字搜索：</span>
									<b-form-input size="sm" class="mr-sm-2" placeholder="疫苗编号搜索" v-model="query['vaccine_no']" />
									<b-form-input size="sm" class="mr-sm-2" placeholder="疫苗名称搜索" v-model="query['vaccine_name']" />
									<b-form-input size="sm" class="mr-sm-2" placeholder="疫苗分类搜索" v-model="query['vaccine_classification']" />
												<b-form-input size="sm" class="mr-sm-2" placeholder="可预约地点搜索" v-model="query['bookable_location']" />
										<b-button size="sm" @click="search()" >
								<b-icon icon="search"/>
							</b-button>
						</div>
						<!-- /搜索栏 -->
					</div>
				</div>
				<div class="diy_list_select_box">
					<span class="diy_list_select_title">下拉搜索：</span>
						<div class="diy_list_dropdown_box">
						<div class="col">
							<!-- 筛选 -->
							<div class="view">
																				<b-dropdown text="疫苗分类" variant="outline-dark" left>
									<b-dropdown-item @click="filter_set('全部','vaccine_classification')">全部</b-dropdown-item>
										<b-dropdown-item v-for="(o, i) in list_vaccine_classification" :key="i" @click="filter_set(o['vaccine_classification'],'vaccine_classification')" >
												{{ o['vaccine_classification'] }}
										</b-dropdown-item>
								</b-dropdown>
																	<b-dropdown text="可预约地点" variant="outline-dark" left>
									<b-dropdown-item @click="filter_set('全部','bookable_location')">全部</b-dropdown-item>
										<b-dropdown-item v-for="(o, i) in list_bookable_location" :key="i" @click="filter_set(o['bookable_location'],'bookable_location')" >
												{{ o['bookable_location'] }}
										</b-dropdown-item>
								</b-dropdown>
												</div>
							<!-- /筛选 -->
						</div>
					</div>
					<div class="diy_list_sort_box">
						<div class="col">
							<!-- 排序 -->
							<div class="view">
								<b-dropdown text="排序" variant="outline-dark" left>
										<b-dropdown-item v-for="(o, i) in list_sort" :key="i" @click="set_sort(o)" >
												{{ o.name }}
										</b-dropdown-item>
								</b-dropdown>
							</div>
							<!--/排序 -->
						</div>
					</div>
				</div>

				<div class="row diy_list_box">
					<div class="col">
						<!-- 列表 -->
						<list_vaccine_appointment :list="list" />
						<!-- /列表 -->
					</div>
				</div>
				<div class="row diy_list_page_box">
					<div class="col overflow-auto flex_cc">
						<!-- 分页器 -->
<!--						<diy_pager v-model="query['page']" :size="query['size']" :count="count" v-on:toPage="toPage" v-on:toSize="toSize" ></diy_pager>-->
            <b-pagination
                v-model="query.page"
                :total-rows="count"
                :per-page="query.size"
                @change="goToPage"
            />
						<!-- /分页器 -->
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import list_vaccine_appointment from "@/components/diy/list_vaccine_appointment.vue";
	import diy_pager from "@/components/diy/diy_pager";
	import mixin from "@/mixins/page.js";

	export default {
		mixins: [mixin],
		components: {
			diy_pager,
			list_vaccine_appointment
		},
		data() {
			return {
				url_get_list: "~/api/vaccine_appointment/get_list?like=0",

				// 查询条件
				query: {
					keyword: "",
					page: 1,
					size: 10,
									"vaccine_no": "", // 疫苗编号
											"vaccine_name": "", // 疫苗名称
											"vaccine_classification": "", // 疫苗分类
														"bookable_location": "", // 可预约地点
									},

				// 排序内容
				list_sort: [{
						name: "创建时间从高到低",
						value: "create_time desc",
					},
					{
						name: "创建时间从低到高",
						value: "create_time asc",
					},
					{
						name: "更新时间从高到低",
						value: "update_time desc",
					},
					{
						name: "更新时间从低到高",
						value: "update_time asc",
					},
							{
						name: "疫苗编号正序",
						value: "vaccine_no asc",
					},
					{
						name: "疫苗编号倒序",
						value: "vaccine_no desc",
					},
							{
						name: "疫苗名称正序",
						value: "vaccine_name asc",
					},
					{
						name: "疫苗名称倒序",
						value: "vaccine_name desc",
					},
							{
						name: "疫苗分类正序",
						value: "vaccine_classification asc",
					},
					{
						name: "疫苗分类倒序",
						value: "vaccine_classification desc",
					},
										{
						name: "可预约地点正序",
						value: "bookable_location asc",
					},
					{
						name: "可预约地点倒序",
						value: "bookable_location desc",
					},
							],

											// 疫苗分类列表
				"list_vaccine_classification": [""],
													// 可预约地点列表
				"list_bookable_location": [""],
						
			}
		},
		methods: {
      get_list_before(param) {
      },
			/**
			 * 筛选选择
			 */
			filter_set(o,key) {
			    if (o == "全部") {
			        this.query[key] = "";
			    } else {
			        this.query[key] = o;
			    }
			    this.search();
			},

			/**
			 * 排序
			 */
			set_sort(o) {
			    this.query.orderby = o.value;
			    this.search();
			},


							/**
			 * 获取疫苗分类列表
			 */
			async get_list_vaccine_classification() {
				var json = await this.$get("~/api/vaccine_classification/get_list?");
				if (json.result) {
					this.list_vaccine_classification = json.result.list;
				} else if (json.error) {
					console.log(json.error);
				}
			},
								/**
			 * 获取可预约地点列表
			 */
			async get_list_bookable_location() {
				var json = await this.$get("~/api/bookable_location/get_list?");
				if (json.result) {
					this.list_bookable_location = json.result.list;
				} else if (json.error) {
					console.log(json.error);
				}
			},
			
			/**
			 * 筛选
			 */
																			filter_vaccine_classification(o) {
				if (o == "全部") {
					this.query["vaccine_classification"] = "";
				} else {
					this.query["vaccine_classification"] = o;
				}
				this.search();
			},
														filter_bookable_location(o) {
				if (o == "全部") {
					this.query["bookable_location"] = "";
				} else {
					this.query["bookable_location"] = o;
				}
				this.search();
			},
										/**
			 * 重置
			 */
			reset() {
								this.query.vaccine_no = ""
										this.query.vaccine_name = ""
										this.query.vaccine_classification = ""
													this.query.bookable_location = ""
									this.search();
			},

			// 返回条数
			toSize(i){
				this.query.size = i;
				this.first();
			},

			// 返回页数
			toPage(i){
				this.query.page = i;
				this.first();
			},

      goToPage(v){
        this.query.page = v;
        this.goToNew(v)
      },

		},
		computed: {
		},
		created() {
										/**
			 * 获取疫苗分类列表
			 */
			this.get_list_vaccine_classification();
												/**
			 * 获取可预约地点列表
			 */
			this.get_list_bookable_location();
								}
	}
</script>

<style>
</style>
